"use client";

import React, { useState } from "react";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Textarea } from "./ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";

interface CreativeGeneratorProps {
  type: "music" | "art" | "video" | "sfx";
}

interface GeneratedContent {
  id: string;
  type: string;
  prompt: string;
  result: string;
  timestamp: Date;
  status: "generating" | "completed" | "error";
}

const CreativeGenerator: React.FC<CreativeGeneratorProps> = ({ type }) => {
  const [prompt, setPrompt] = useState("");
  const [isGenerating, setIsGenerating] = useState(false);
  const [generatedContent, setGeneratedContent] = useState<GeneratedContent[]>([]);

  const typeConfig = {
    music: {
      title: "Music Generation",
      description: "Create original music tracks and melodies",
      placeholder: "Describe the music you want to create (e.g., 'upbeat electronic dance music with piano')",
      examples: [
        "Peaceful piano melody for relaxation",
        "Energetic rock guitar riff",
        "Ambient electronic soundscape",
        "Classical orchestral piece"
      ]
    },
    art: {
      title: "Art Generation",
      description: "Generate digital artwork and images",
      placeholder: "Describe the artwork you want to create (e.g., 'anime-style character with blue hair')",
      examples: [
        "Anime boy with cyan hair and blue eyes",
        "Futuristic cityscape at sunset",
        "Magical forest with glowing trees",
        "Abstract geometric patterns"
      ]
    },
    video: {
      title: "Video Generation",
      description: "Create animated videos and clips",
      placeholder: "Describe the video you want to create (e.g., 'short animation of a character waving')",
      examples: [
        "Character walking animation loop",
        "Particle effects explosion",
        "Smooth camera pan across landscape",
        "Text animation with effects"
      ]
    },
    sfx: {
      title: "Sound Effects Generation",
      description: "Generate custom sound effects and audio",
      placeholder: "Describe the sound effect you want (e.g., 'magical sparkle sound')",
      examples: [
        "Magical sparkle and chime sounds",
        "Futuristic beep and notification sounds",
        "Nature sounds like rain and wind",
        "Mechanical and robotic sounds"
      ]
    }
  };

  const config = typeConfig[type];

  const generateContent = async () => {
    if (!prompt.trim()) return;

    setIsGenerating(true);
    
    const newContent: GeneratedContent = {
      id: `gen_${Date.now()}`,
      type,
      prompt,
      result: "",
      timestamp: new Date(),
      status: "generating"
    };

    setGeneratedContent(prev => [newContent, ...prev]);

    // Simulate generation process
    setTimeout(() => {
      const simulatedResults = {
        music: [
          "🎵 Generated a beautiful piano melody in C major",
          "🎶 Created an upbeat electronic track with synthesizers",
          "🎼 Composed a peaceful ambient soundscape",
          "🎹 Generated a classical orchestral arrangement"
        ],
        art: [
          "🎨 Created a stunning anime-style character illustration",
          "🖼️ Generated a beautiful landscape painting",
          "✨ Designed abstract geometric artwork",
          "🌟 Created a magical fantasy scene"
        ],
        video: [
          "🎬 Generated a smooth character animation",
          "📹 Created particle effects sequence",
          "🎥 Produced a cinematic camera movement",
          "⚡ Generated dynamic text animation"
        ],
        sfx: [
          "🔊 Created magical sparkle sound effects",
          "📢 Generated futuristic notification sounds",
          "🌧️ Produced realistic nature sound effects",
          "🤖 Created mechanical and robotic audio"
        ]
      };

      const randomResult = simulatedResults[type][Math.floor(Math.random() * simulatedResults[type].length)];

      setGeneratedContent(prev => 
        prev.map(item => 
          item.id === newContent.id 
            ? { ...item, result: randomResult, status: "completed" as const }
            : item
        )
      );
      setIsGenerating(false);
      setPrompt("");
    }, 2000 + Math.random() * 3000);
  };

  const useExample = (example: string) => {
    setPrompt(example);
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h2 className="text-2xl font-bold text-white mb-2">{config.title}</h2>
        <p className="text-gray-300">{config.description}</p>
      </div>

      {/* Generation Interface */}
      <Card className="bg-gray-800 border-cyan-500">
        <CardHeader>
          <CardTitle className="text-cyan-300">Create New Content</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <Textarea
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            placeholder={config.placeholder}
            className="bg-gray-900 text-white border-gray-600 min-h-20"
          />
          
          <div className="flex justify-between items-center">
            <Button
              onClick={generateContent}
              disabled={!prompt.trim() || isGenerating}
              className="bg-cyan-600 hover:bg-cyan-700 text-white"
            >
              {isGenerating ? "Generating..." : `Generate ${type.charAt(0).toUpperCase() + type.slice(1)}`}
            </Button>
            
            <span className="text-sm text-gray-400">
              {generatedContent.length} items created
            </span>
          </div>
        </CardContent>
      </Card>

      {/* Example Prompts */}
      <Card className="bg-gray-800 border-purple-500">
        <CardHeader>
          <CardTitle className="text-purple-300">Example Prompts</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
            {config.examples.map((example, index) => (
              <Button
                key={index}
                variant="outline"
                onClick={() => useExample(example)}
                className="text-left justify-start h-auto p-3 border-gray-600 text-gray-300 hover:text-white hover:border-purple-500"
              >
                {example}
              </Button>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Generated Content History */}
      {generatedContent.length > 0 && (
        <Card className="bg-gray-800 border-green-500">
          <CardHeader>
            <CardTitle className="text-green-300">Generated Content</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-3 max-h-64 overflow-y-auto">
              {generatedContent.map((content) => (
                <div key={content.id} className="bg-gray-900 p-3 rounded border border-gray-700">
                  <div className="flex justify-between items-start mb-2">
                    <span className={`px-2 py-1 rounded text-xs ${
                      content.status === "generating" ? "bg-yellow-600 text-white" :
                      content.status === "completed" ? "bg-green-600 text-white" :
                      "bg-red-600 text-white"
                    }`}>
                      {content.status}
                    </span>
                    <span className="text-xs text-gray-500">
                      {content.timestamp.toLocaleTimeString()}
                    </span>
                  </div>
                  
                  <p className="text-sm text-gray-400 mb-2">
                    <strong>Prompt:</strong> {content.prompt}
                  </p>
                  
                  {content.result && (
                    <p className="text-sm text-white">
                      <strong>Result:</strong> {content.result}
                    </p>
                  )}
                  
                  {content.status === "generating" && (
                    <div className="flex items-center space-x-2 mt-2">
                      <div className="w-2 h-2 bg-cyan-400 rounded-full animate-bounce"></div>
                      <div className="w-2 h-2 bg-cyan-400 rounded-full animate-bounce" style={{ animationDelay: "0.1s" }}></div>
                      <div className="w-2 h-2 bg-cyan-400 rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
                      <span className="text-xs text-cyan-400 ml-2">Cyan is creating...</span>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Cyan's Creative Notes */}
      <Card className="bg-gray-800 border-cyan-500">
        <CardHeader>
          <CardTitle className="text-cyan-300">Cyan's Creative Notes</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-sm text-gray-300 space-y-2">
            <p>💡 <strong>Tip:</strong> Be specific in your descriptions for better results!</p>
            <p>🎨 <strong>Remember:</strong> I learn from each creation to make the next one even better!</p>
            <p>✨ <strong>Fun fact:</strong> I can combine different styles and elements in creative ways!</p>
            <p>🚀 <strong>Coming soon:</strong> Real AI-powered generation with advanced models!</p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default CreativeGenerator;
