"use client";

import React, { useState, useEffect } from "react";
import { Button } from "./ui/button";
import { Slider } from "./ui/slider";

interface VoiceConfig {
  pitch: number;
  rate: number;
  volume: number;
  voice: string;
  emotion: "happy" | "excited" | "curious" | "thoughtful" | "concerned" | "playful";
}

interface VoiceSynthesisProps {
  text: string;
  emotion?: VoiceConfig["emotion"];
  autoSpeak?: boolean;
  onSpeakStart?: () => void;
  onSpeakEnd?: () => void;
}

const VoiceSynthesis: React.FC<VoiceSynthesisProps> = ({
  text,
  emotion = "happy",
  autoSpeak = false,
  onSpeakStart,
  onSpeakEnd
}) => {
  const [isSupported, setIsSupported] = useState(false);
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [voices, setVoices] = useState<SpeechSynthesisVoice[]>([]);
  const [voiceConfig, setVoiceConfig] = useState<VoiceConfig>({
    pitch: 1.8, // Higher pitch for child-like voice
    rate: 1.1, // Slightly faster for energetic feel
    volume: 0.8,
    voice: "",
    emotion: "happy"
  });

  // Check for speech synthesis support
  useEffect(() => {
    if (typeof window !== "undefined" && "speechSynthesis" in window) {
      setIsSupported(true);
      
      // Load available voices
      const loadVoices = () => {
        const availableVoices = speechSynthesis.getVoices();
        setVoices(availableVoices);
        
        // Try to find a suitable voice for a young character
        const preferredVoice = availableVoices.find(voice => 
          voice.name.toLowerCase().includes("child") ||
          voice.name.toLowerCase().includes("young") ||
          voice.name.toLowerCase().includes("female") ||
          voice.lang.startsWith("en")
        );
        
        if (preferredVoice) {
          setVoiceConfig(prev => ({ ...prev, voice: preferredVoice.name }));
        }
      };

      loadVoices();
      speechSynthesis.onvoiceschanged = loadVoices;
    }
  }, []);

  // Update voice config based on emotion
  useEffect(() => {
    setVoiceConfig(prev => {
      const emotionConfigs = {
        happy: { pitch: 1.8, rate: 1.1, volume: 0.8 },
        excited: { pitch: 2.0, rate: 1.3, volume: 0.9 },
        curious: { pitch: 1.9, rate: 1.0, volume: 0.7 },
        thoughtful: { pitch: 1.6, rate: 0.9, volume: 0.6 },
        concerned: { pitch: 1.7, rate: 0.8, volume: 0.7 },
        playful: { pitch: 2.1, rate: 1.2, volume: 0.8 }
      };
      
      return { ...prev, ...emotionConfigs[emotion], emotion };
    });
  }, [emotion]);

  // Auto-speak when text changes
  useEffect(() => {
    if (autoSpeak && text && isSupported) {
      speak();
    }
  }, [text, autoSpeak, isSupported]);

  const speak = () => {
    if (!isSupported || !text || isSpeaking) return;

    // Cancel any ongoing speech
    speechSynthesis.cancel();

    const utterance = new SpeechSynthesisUtterance(text);
    
    // Apply voice configuration
    utterance.pitch = voiceConfig.pitch;
    utterance.rate = voiceConfig.rate;
    utterance.volume = voiceConfig.volume;
    
    // Set voice if available
    if (voiceConfig.voice) {
      const selectedVoice = voices.find(voice => voice.name === voiceConfig.voice);
      if (selectedVoice) {
        utterance.voice = selectedVoice;
      }
    }

    // Add emotional pauses and expressions
    let emotionalText = text;
    switch (voiceConfig.emotion) {
      case "excited":
        emotionalText = text.replace(/[.!?]/g, "! ");
        break;
      case "curious":
        emotionalText = text.replace(/\./g, "? ");
        break;
      case "thoughtful":
        emotionalText = text.replace(/\./g, "... ");
        break;
      case "playful":
        emotionalText = text.replace(/\s/g, " *giggles* ");
        break;
    }
    
    utterance.text = emotionalText;

    // Event handlers
    utterance.onstart = () => {
      setIsSpeaking(true);
      onSpeakStart?.();
    };

    utterance.onend = () => {
      setIsSpeaking(false);
      onSpeakEnd?.();
    };

    utterance.onerror = () => {
      setIsSpeaking(false);
      console.error("Speech synthesis error");
    };

    speechSynthesis.speak(utterance);
  };

  const stopSpeaking = () => {
    speechSynthesis.cancel();
    setIsSpeaking(false);
  };

  if (!isSupported) {
    return (
      <div className="text-sm text-gray-500">
        Voice synthesis not supported in this browser
      </div>
    );
  }

  return (
    <div className="space-y-4 p-4 bg-gray-900 rounded-lg border border-cyan-500">
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-semibold text-cyan-300">Cyan's Voice</h3>
        <div className="flex space-x-2">
          <Button
            onClick={speak}
            disabled={isSpeaking || !text}
            className="bg-cyan-600 hover:bg-cyan-700 text-white"
            size="sm"
          >
            {isSpeaking ? "Speaking..." : "Speak"}
          </Button>
          <Button
            onClick={stopSpeaking}
            disabled={!isSpeaking}
            variant="outline"
            className="border-red-500 text-red-400 hover:bg-red-500 hover:text-white"
            size="sm"
          >
            Stop
          </Button>
        </div>
      </div>

      {/* Voice Controls */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div>
          <label className="text-sm text-gray-300 mb-2 block">Pitch</label>
          <Slider
            value={[voiceConfig.pitch]}
            onValueChange={(value) => setVoiceConfig(prev => ({ ...prev, pitch: value[0] }))}
            min={0.5}
            max={2.5}
            step={0.1}
            className="w-full"
          />
          <span className="text-xs text-gray-400">{voiceConfig.pitch.toFixed(1)}</span>
        </div>

        <div>
          <label className="text-sm text-gray-300 mb-2 block">Speed</label>
          <Slider
            value={[voiceConfig.rate]}
            onValueChange={(value) => setVoiceConfig(prev => ({ ...prev, rate: value[0] }))}
            min={0.5}
            max={2.0}
            step={0.1}
            className="w-full"
          />
          <span className="text-xs text-gray-400">{voiceConfig.rate.toFixed(1)}</span>
        </div>

        <div>
          <label className="text-sm text-gray-300 mb-2 block">Volume</label>
          <Slider
            value={[voiceConfig.volume]}
            onValueChange={(value) => setVoiceConfig(prev => ({ ...prev, volume: value[0] }))}
            min={0.1}
            max={1.0}
            step={0.1}
            className="w-full"
          />
          <span className="text-xs text-gray-400">{voiceConfig.volume.toFixed(1)}</span>
        </div>
      </div>

      {/* Voice Selection */}
      {voices.length > 0 && (
        <div>
          <label className="text-sm text-gray-300 mb-2 block">Voice</label>
          <select
            value={voiceConfig.voice}
            onChange={(e) => setVoiceConfig(prev => ({ ...prev, voice: e.target.value }))}
            className="w-full bg-gray-800 text-white border border-gray-600 rounded p-2"
          >
            <option value="">Default Voice</option>
            {voices.map((voice) => (
              <option key={voice.name} value={voice.name}>
                {voice.name} ({voice.lang})
              </option>
            ))}
          </select>
        </div>
      )}

      {/* Emotion Indicator */}
      <div className="flex items-center space-x-2">
        <span className="text-sm text-gray-300">Current Emotion:</span>
        <span className={`px-2 py-1 rounded text-xs font-medium ${
          voiceConfig.emotion === "excited" ? "bg-yellow-600 text-white" :
          voiceConfig.emotion === "curious" ? "bg-purple-600 text-white" :
          voiceConfig.emotion === "thoughtful" ? "bg-blue-600 text-white" :
          voiceConfig.emotion === "concerned" ? "bg-red-600 text-white" :
          voiceConfig.emotion === "playful" ? "bg-pink-600 text-white" :
          "bg-green-600 text-white"
        }`}>
          {voiceConfig.emotion}
        </span>
      </div>

      {/* Preview Text */}
      {text && (
        <div className="bg-black p-3 rounded border border-gray-700">
          <p className="text-sm text-gray-300 mb-2">Text to speak:</p>
          <p className="text-white">{text}</p>
        </div>
      )}
    </div>
  );
};

export default VoiceSynthesis;
